body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  outline: none;
}
body {
  background: oldlace;
}

input,textarea {
  resize: none;
}
.carousel-inner>.item>a>img, .carousel-inner>.item>img {
  margin: 0 auto;
}

.page-main {
  display: -webkit-flex;
  display: flex;
  position: relative;
}
.constructor-area {
  padding-left: 200px;
  max-width: 100%;
  width: 100vw;
  height: 100vh;
}
.ctr-area-inner {
  width: 100%;
  height: 100%;
}
.elements-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 10px 10px 10px 0;
  background: papayawhip;
  box-shadow: 5px 0px 10px 0px rgba(0, 0, 0, 0.2);
}
.element-list {
  cursor: pointer;
  padding: 5px 10px;
  background: rgb(44, 147, 243);
  color: white;
  border-radius: 4px;
  margin-bottom: 5px;
}

/* Editor wrapper */
.editor-wrapper {
  box-shadow: inset 0 0 0 0 transparent;
  position: relative;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.editor-wrapper:hover {
  box-shadow: inset 0 0 0 3px rgba(0,0,0,.7);
  -webkit-transition: all .3s;
  transition: all .3s;
}
.panel-editor {
  display: none;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
.editor-wrapper:hover .panel-editor{
  display: -webkit-flex;
  display: flex;
}
.move-btns,
.right-panel-btns {
  display: -webkit-flex;
  display: flex;
}
.panel-btn {
  width: 30px;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
  background-color: rgba(0,0,0,.7);
  -webkit-transition: all .3s;
  transition: all .3s;
  color: #fff;
}
.panel-btn:hover {
  background-color: rgba(0,0,0,.4);
  -webkit-transition: all .3s;
  transition: all .3s;
}

.editor-wrapper textarea {
  height: 250px;
}
.tab-pane {
  padding: 10px 0;
}
.modal-body {
  padding-left: 0;
  padding-right: 0;
}
.modal-body .tab-content {
  padding-left: 15px;
  padding-right: 15px;
}
